import { useState, useEffect } from 'react';
import { useSearchParams, useNavigate } from 'react-router-dom';
import { Button } from '@/components/ui/button';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Textarea } from '@/components/ui/textarea';
import { AIDesignWizard } from '@/components/AIDesignWizard';
import { useContentGeneration } from '@/hooks/useContentGeneration';
import { BusinessData } from '@/types/business';
import { ContentGenerationRequest } from '@/types/content';

function ContentGeneration() {
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const [businessData, setBusinessData] = useState<BusinessData | null>(null);
  const [request, setRequest] = useState<Partial<ContentGenerationRequest>>({
    tone: 'professional',
    style: 'modern',
    targetAudience: '',
    specialRequests: ''
  });
  const [showAIDesignWizard, setShowAIDesignWizard] = useState(false);
  const [aiTheme, setAiTheme] = useState<any>(null);

  const { generateContent, isLoading, generatedContent, error } = useContentGeneration();

  useEffect(() => {
    const businessDataParam = searchParams.get('businessData');
    if (businessDataParam) {
      try {
        const parsed = JSON.parse(businessDataParam);
        setBusinessData(parsed);
        setRequest(prev => ({ ...prev, businessData: parsed }));
      } catch (error) {
        console.error('Failed to parse business data:', error);
        navigate('/onboarding');
      }
    } else {
      navigate('/onboarding');
    }
  }, [searchParams, navigate]);

  if (!businessData) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900">
        <div className="text-center">
          <h2 className="text-2xl font-semibold mb-2 text-white">Loading...</h2>
          <p className="text-gray-400">Preparing your business information</p>
        </div>
      </div>
    );
  }

  const handleGenerate = async () => {
    try {
      const content = await generateContent(request as ContentGenerationRequest);
      // Navigate to editor with generated content
      const params = new URLSearchParams({
        businessData: JSON.stringify(businessData),
        generatedContent: JSON.stringify(content)
      });
      navigate(`/editor?${params.toString()}`);
    } catch (err) {
      console.error('Content generation failed:', err);
    }
  };

  const updateRequest = (field: keyof ContentGenerationRequest, value: string) => {
    setRequest(prev => ({ ...prev, [field]: value }));
  };

  const handleContinueToEditor = () => {
    if (generatedContent) {
      const params = new URLSearchParams({
        businessData: JSON.stringify(businessData),
        generatedContent: JSON.stringify(generatedContent)
      });
      navigate(`/editor?${params.toString()}`);
    }
  };

  const handleApplyAITheme = (theme: any) => {
    setAiTheme(theme);
    setShowAIDesignWizard(false);
    // Update the generated content with the AI theme if available
    if (generatedContent) {
      // Trigger regeneration with new theme applied
      console.log('Applied AI theme:', theme);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900 relative overflow-hidden">
      {/* Background Effects */}
      <div className="absolute inset-0 overflow-hidden">
        <div className="absolute -top-40 -right-40 w-80 h-80 bg-blue-500/10 rounded-full blur-3xl"></div>
        <div className="absolute -bottom-40 -left-40 w-80 h-80 bg-purple-500/10 rounded-full blur-3xl"></div>
        <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-96 h-96 bg-indigo-500/5 rounded-full blur-3xl"></div>
      </div>

      {/* Navigation */}
      <nav className="relative z-10 flex justify-between items-center p-6 max-w-7xl mx-auto">
        <div className="text-white font-bold text-xl">
          Landify
        </div>
        <div className="flex items-center space-x-8">
          <button 
            onClick={() => navigate('/onboarding')}
            className="text-gray-300 hover:text-white transition-colors"
          >
             Back to Business Info
          </button>
        </div>
      </nav>

      <div className="relative z-10 flex flex-col items-center justify-center p-8 pt-20">
        <div className="w-full max-w-4xl">
          <div className="text-center mb-8">
            <h1 className="text-4xl md:text-5xl font-bold text-white mb-4">
              Customize Your
              <br />
              <span className="bg-gradient-to-r from-blue-400 to-purple-400 bg-clip-text text-transparent">
                AI Content
              </span>
            </h1>
            <p className="text-xl text-gray-300">
              Tell us about your preferences so we can create the perfect content for your landing page
            </p>
          </div>

          <div className="backdrop-blur-sm bg-white/5 border border-white/10 rounded-xl p-8 shadow-2xl">
            <div className="text-center mb-6">
              <h2 className="text-2xl font-semibold text-white">Content Preferences</h2>
              <p className="text-gray-300 mt-2">Choose how you want your content to sound and feel</p>
            </div>
            
            <div className="space-y-6">
              {/* Business Summary */}
              <div className="bg-gradient-to-r from-blue-500/20 to-purple-500/20 border border-blue-400/30 rounded-xl p-6">
                <h3 className="font-semibold mb-3 text-white flex items-center">
                  <svg className="w-5 h-5 mr-2 text-blue-400" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M4 4a2 2 0 012-2h8a2 2 0 012 2v12a1 1 0 110 2h-3a1 1 0 01-1-1v-6a1 1 0 00-1-1H9a1 1 0 00-1 1v6a1 1 0 01-1 1H4a1 1 0 110-2V4zm3 1h2v2H7V5zm2 4H7v2h2V9zm2-4h2v2h-2V5zm2 4h-2v2h2V9z" clipRule="evenodd"/>
                  </svg>
                  Business Summary
                </h3>
                <div className="space-y-2 text-sm">
                  <p className="text-blue-200"><strong className="text-blue-100">Name:</strong> {businessData.name}</p>
                  <p className="text-blue-200"><strong className="text-blue-100">Type:</strong> {businessData.type}</p>
                  <p className="text-blue-200"><strong className="text-blue-100">Location:</strong> {businessData.address}</p>
                  {businessData.rating && (
                    <p className="text-blue-200"><strong className="text-blue-100">Rating:</strong> {businessData.rating} ({businessData.reviews} reviews)</p>
                  )}
                </div>
              </div>

              {/* Content Preferences */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {/* Tone Selection */}
                <div className="space-y-3">
                  <Label htmlFor="tone" className="text-white font-medium">Content Tone</Label>
                  <Select
                    value={request.tone}
                    onValueChange={(value) => updateRequest('tone', value)}
                  >
                    <SelectTrigger className="bg-white/10 border-white/20 text-white">
                      <SelectValue placeholder="Select tone" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="professional">Professional</SelectItem>
                      <SelectItem value="friendly">Friendly</SelectItem>
                      <SelectItem value="casual">Casual</SelectItem>
                      <SelectItem value="luxury">Luxury</SelectItem>
                    </SelectContent>
                  </Select>
                </div>

                {/* Style Selection */}
                <div className="space-y-3">
                  <Label htmlFor="style" className="text-white font-medium">Design Style</Label>
                  <Select
                    value={request.style}
                    onValueChange={(value) => updateRequest('style', value)}
                  >
                    <SelectTrigger className="bg-white/10 border-white/20 text-white">
                      <SelectValue placeholder="Select style" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="modern">Modern</SelectItem>
                      <SelectItem value="classic">Classic</SelectItem>
                      <SelectItem value="minimalist">Minimalist</SelectItem>
                      <SelectItem value="bold">Bold</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              </div>

              {/* Target Audience */}
              <div className="space-y-3">
                <Label htmlFor="targetAudience" className="text-white font-medium">Target Audience (Optional)</Label>
                <Textarea
                  id="targetAudience"
                  placeholder="Describe your ideal customers (e.g., young professionals, families, seniors, etc.)"
                  value={request.targetAudience}
                  onChange={(e) => updateRequest('targetAudience', e.target.value)}
                  className="bg-white/10 border-white/20 text-white placeholder:text-gray-400 focus:bg-white/20 focus:border-white/40 transition-all duration-300"
                  rows={3}
                />
              </div>

              {/* Special Requests */}
              <div className="space-y-3">
                <Label htmlFor="specialRequests" className="text-white font-medium">Special Requests (Optional)</Label>
                <Textarea
                  id="specialRequests"
                  placeholder="Any specific content requirements, keywords to include, or messaging preferences"
                  value={request.specialRequests}
                  onChange={(e) => updateRequest('specialRequests', e.target.value)}
                  className="bg-white/10 border-white/20 text-white placeholder:text-gray-400 focus:bg-white/20 focus:border-white/40 transition-all duration-300"
                  rows={3}
                />
              </div>

              {/* AI Design Assistant */}
              <div className="bg-gradient-to-r from-purple-500/20 to-pink-500/20 border border-purple-400/30 rounded-xl p-6">
                <h3 className="font-semibold text-purple-300 mb-3 flex items-center">
                  <svg className="w-5 h-5 mr-2 text-purple-400" fill="currentColor" viewBox="0 0 20 20">
                    <path d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z"/>
                  </svg>
                  AI Design Assistant
                </h3>
                <p className="text-purple-200 text-sm mb-4">
                  Get custom design recommendations powered by AI. Our design assistant will analyze your business and create a unique visual identity.
                </p>
                {aiTheme && (
                  <div className="bg-purple-500/20 border border-purple-400/20 rounded-lg p-3 mb-4">
                    <p className="text-purple-200 text-sm">
                       <strong>AI Theme Applied:</strong> {aiTheme.name}
                    </p>
                  </div>
                )}
                <Button 
                  onClick={() => setShowAIDesignWizard(true)}
                  variant="outline"
                  className="w-full border-purple-400/50 text-purple-300 hover:bg-purple-500/20 hover:text-purple-200 transition-all duration-300"
                >
                   Open AI Design Assistant
                </Button>
              </div>

              {/* Generate Button */}
              <div className="grid grid-cols-1 gap-4">
                <Button 
                  onClick={handleGenerate} 
                  disabled={isLoading}
                  className="w-full bg-gradient-to-r from-blue-500 to-purple-500 hover:from-blue-600 hover:to-purple-600 text-white font-medium py-3 rounded-lg transition-all duration-300 shadow-lg hover:shadow-xl"
                  size="lg"
                >
                  {isLoading ? (
                    <div className="flex items-center">
                      <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                        <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                        <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                      </svg>
                      Generating Content...
                    </div>
                  ) : 'Generate Landing Page Content'}
                </Button>
              </div>

              {/* Error Display */}
              {error && (
                <div className="bg-red-500/20 border border-red-400/30 rounded-xl p-4">
                  <div className="flex items-center">
                    <svg className="w-5 h-5 text-red-400 mr-2" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7 4a1 1 0 11-2 0 1 1 0 012 0zm-1-9a1 1 0 00-1 1v4a1 1 0 102 0V6a1 1 0 00-1-1z" clipRule="evenodd"/>
                    </svg>
                    <p className="text-red-300 font-medium">
                      Error generating content: {error.message}
                    </p>
                  </div>
                </div>
              )}

              {/* Generated Content Preview */}
              {generatedContent && (
                <div className="bg-gradient-to-r from-green-500/20 to-emerald-500/20 border border-green-400/30 rounded-xl p-6">
                  <h3 className="font-semibold text-green-400 mb-4 flex items-center">
                    <svg className="w-5 h-5 mr-2" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd"/>
                    </svg>
                    Content Generated Successfully!
                  </h3>
                  <div className="space-y-4 mb-6">
                    <div>
                      <h4 className="font-semibold text-green-300 mb-1">Headline:</h4>
                      <p className="text-green-200">{generatedContent.headline}</p>
                    </div>
                    <div>
                      <h4 className="font-semibold text-green-300 mb-1">Subheadline:</h4>
                      <p className="text-green-200">{generatedContent.subheadline}</p>
                    </div>
                    <div>
                      <h4 className="font-semibold text-green-300 mb-1">Value Propositions:</h4>
                      <ul className="list-disc list-inside text-green-200 space-y-1">
                        {generatedContent.valuePropositions.map((prop: string, index: number) => (
                          <li key={index}>{prop}</li>
                        ))}
                      </ul>
                    </div>
                  </div>
                  <Button 
                    onClick={handleContinueToEditor}
                    className="w-full bg-gradient-to-r from-green-500 to-emerald-500 hover:from-green-600 hover:to-emerald-600 text-white font-medium py-3 rounded-lg transition-all duration-300 shadow-lg hover:shadow-xl"
                  >
                    Continue to Landing Page Editor
                  </Button>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
      
      {/* AI Design Wizard Modal */}
      {showAIDesignWizard && businessData && (
        <AIDesignWizard
          businessData={businessData}
          onApplyTheme={handleApplyAITheme}
          onClose={() => setShowAIDesignWizard(false)}
        />
      )}
    </div>
  );
}

export default ContentGeneration; 